.NavBar_Main {
    background-image: linear-gradient(to bottom, #044c02, #3a6903);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px;
    .NavBar_main_logo {
        margin-left: 10px;
        a {
            font-size: 20px;
            color: white;
            text-decoration-color: #ffc966;
            font-size: 36px;
            font-family: "JacquesFrancois";
        }
    }
    .NavBar_main_links {
        a {
            font-size: 20px;
            text-decoration: none;
            color: white;
            margin: 25px;
            font-family: "Kalnia";
        }
    }
}

//Styles for phone
@media (max-width: 1280px) {
    .NavBar_Main {
        display: flex;
        .NavBar_main_logo {
            & img {
            }
        }
    }
}
